/* major breakpoints */
@custom-media --sp (max-width: 767px);
@custom-media --tb (min-width: 768px) and (max-width: 1023px);
@custom-media --tb-h (min-width: 1024px) and (max-width: 1279px);
@custom-media --not-sp (min-width: 768px);
:root {
  --app-height: 100%;
}

#main .content .filter .filter-item {
  border-bottom: 1px solid #ccc;
  display: flex;
}
#main .content .filter .filter-item .filter-title {
  font-size: 2.6rem;
  width: 240px;
  padding-top: 8px;
  padding-bottom: 8px;
}
#main .content .filter .filter-item .filter-list {
  display: flex;
  align-items: center;
  flex: 1;
}
#main .content .filter .filter-item .filter-list li {
  padding-top: 8px;
  padding-bottom: 8px;
  margin-right: 24px;
}
#main .content .filter .filter-item .filter-list li label {
  font-size: 2rem;
  cursor: pointer;
}
#main .content .filter .filter-item .filter-list li label input {
  margin-right: 8px;
}
#main .content .property {
  margin-top: 64px;
}
#main .content .property .property-head.-hide {
  display: none;
}

#main {
  background-color: #fff;
  padding-top: 80px;
  padding-bottom: 80px;
}
#main.properties {
  margin-bottom: 80px;
}
#main .content {
  width: 78.125%;
  max-width: 1000px;
  margin: 0 auto;
}
#main .property-head:not(:first-of-type) {
  margin-top: 64px;
}
#main .property-head .item .item-head {
  display: flex;
  align-items: center;
}
#main .property-head .item .item-head .item-group {
  font-size: 2rem;
  border: 1px solid #000;
  padding: 0 8px;
  margin-right: 32px;
}
#main .property-head .item .item-head .item-title {
  font-size: 2.6rem;
  font-weight: 700;
}
#main .property-head .item .item-body {
  margin-top: 24px;
  display: flex;
}
#main .property-head .item .item-body .item-thumbnail {
  width: 32%;
  max-width: 320px;
}
#main .property-head .item .item-body .item-description {
  flex: 1;
  margin-left: 16px;
}
#main .property-head .item .item-body .item-description table th, #main .property-head .item .item-body .item-description table td {
  padding: 8px;
  background: none;
  border-bottom: 1px solid #ccc;
  text-align: left;
  vertical-align: top;
  font-size: 2rem;
}
#main .property-head .item .item-body .item-description table th {
  color: #000;
}
#main .property-head .item .item-body .item-description table td.-price {
  color: #f00;
}
#main .property-head .item .item-body .item-description table td.-price span {
  font-size: 3.1rem;
}